.containerForm {
  margin-top: 2%;
  width: 50%;
  margin-left: 25%;
  display: flex;
  flex-direction: column;
}

.inputs {
  display: flex;
  align-items: center;
  justify-content: center;
}

.inputs input {
  width: 36%;
  color: var(--roxo);
}

.inputs input:nth-child(2) {
  margin-left: 2%;
}

.inputs input,
.containerForm button {
  background-color: var(--preto);
  height: 40px;
  border-radius: 10px;
  border: none;
  outline: none;
  padding: 0% 2%;
  font-size: 1em;
  transition: 0.6s;
}

.containerForm textarea {
  background-color: var(--preto);
  color: var(--roxo);
  outline: none;
  margin-top: 3%;
  margin-left: 13%;
  padding: 1% 1%;
  font-size: 1.5em;
  width: 74%;
  resize: none;
  outline: none;
  border: none;
  border-radius: 10px;
}

.containerForm button {
  color: var(--branco);
  margin-top: 3%;
  margin-left: 35%;
  cursor: pointer;
  width: 30%;
}

.containerForm button:hover {
  background-color: var(--roxo);
  color: var(--branco);
}

.inputs input:focus {
  border: 1px solid var(--roxo);
}

@media (max-width: 550px) {
  .containerForm {
    width: 100%;
    margin-top: 10%;
    margin-left: 2%;
  }
}
