@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&display=swap);
:root {
  --roxo: rgb(160, 99, 201);
  --branco: rgb(255, 255, 255);
  --preto: rgb(0, 0, 0);
}

body {
  background: rgb(0, 0, 0);
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(0, 0, 0, 1) 35%,
    rgba(31, 30, 31, 1) 100%
  );
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Josefin Sans", sans-serif;
}

span {
  color: rgb(160, 99, 201);
  color: var(--roxo);
}

.leaflet-container {
  width: 80%;
  height: 400px;
  border-radius: 8px;
  box-shadow: 1px 2px 10px 1px rgba(0, 0, 0, 0.514);
}

@media (max-width: 550px) {
  .leaflet-container {
    width: 70%;
  }
}

.leaflet-marker-icon .leaflet-zoom-animated .leaflet-interactive {
  background-color: red;
  color: red;
}

body::-webkit-scrollbar {
  width: 12px;
}

body::-webkit-scrollbar-track {
  background: rgb(255, 255, 255);
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(8, 8, 8);
  border-radius: 20px;
  border: 3px solid rgb(255, 255, 255);
}

.loader-container {
  background-color: black;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.loader-container span {
  color: white;
  margin: 0 1px 100px;
  font-size: 1.8em;
  text-align: center;
}

.loader-container div {
  position: absolute;
  border: 3px solid rgb(160, 99, 201);
  border: 3px solid var(--roxo);
  border-radius: 50%;
  -webkit-animation: loader 2.2s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: loader 2.2s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.ssdsdsdsd{
  width: 95% !important;
}
.loader-container div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}

.loader img {
  width: 55px;
  height: 55px;
}

@-webkit-keyframes loader {
  0% {
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    opacity: 1;
  }

  100% {
    top: 42%;
    left: 55%;
    width: 88px;
    height: 88px;
    opacity: 0;
  }
}

@keyframes loader {
  0% {
    top: 50%;
    left: 50%;
    width: 0;
    height: 0;
    opacity: 1;
  }

  100% {
    top: 42%;
    left: 55%;
    width: 88px;
    height: 88px;
    opacity: 0;
  }
}

body{
  background: var(--preto);
  background: linear-gradient(180deg, var(--preto) 0%, var(--preto) 35%, rgba(31,30,31,1) 100%);
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Josefin Sans', sans-serif;
}

header {
  width: 100%;
  height: 4.5em;
  background-color: var(--preto);
  font-size: 1.1em;
  color: var(--branco);
  display: flex;
  align-items: center;
  justify-content: space-around;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 2000;
}

header img {
  width: 100px;
  height: 40px;
}

li {
  cursor: pointer;
  list-style: none;
  display: inline-block;
  margin: 1% 5px;
}

a {
  text-decoration: none;
  color: var(--branco);
  transition-duration: 0.6s;
}

a:hover,
.store:hover,
.iconHamb:active {
  color: gray;
}

i {
  display: flex;
}

.storeLove {
  font-size: 1.3em;
  cursor: pointer;
  width: 10%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  margin-left: 40%;
}

.link {
  color: white;
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.store {
  transition-duration: 0.6s;
  margin-left: 10%;
}

.heart {
  transition-duration: 0.6s;
}

.heart:hover {
  color: var(--roxo);
}

.iconHamb {
  display: none;
  visibility: hidden;
}

.divMenu {
  display: none;
  visibility: hidden;
}

@media (max-width: 700px) {
  ul {
    display: none;
    margin-top: 5%;
  }

  .iconHamb {
    visibility: visible;
    display: block;
    color: var(--branco);
    cursor: pointer;
    position: absolute;
    right: 0%;
    margin-right: 10%;
    margin-top: 2%;
    z-index: 2000;
  }

  .divMenu {
    visibility: visible;
    text-align: center;
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    height: 300px;
    border-bottom-left-radius: 50%;
    background-color: rgb(0, 0, 0);
    color: white;
    z-index: 101;
    padding: 200px 0px;
  }

  .liMenu {
    display: block;
  }

  .itensMobile {
    display: block;
  }

  .heart,
  .store {
    display: none;
  }

  .heart-2,
  .store-2 {
    display: block;
  }
}

